import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import  {actionAAA,actionText} from '../store/acton1';

class Counter extends React.Component{
    render(){

        const {count,text,actionAAA}=this.props;
        return(
            <div>
                <button onClick={actionAAA}>actionAAA</button>
                <h1>{count}</h1>

               
                <button onClick={()=>this.actionText()}>actionText</button>
                <h1>{text}</h1>

            </div>
        )
    }
     //actionText() this 没有默认绑定 需要箭头函数 或者手动bind 
    actionText(){
        this.props.actionText("666")
    }
}

//组件映射redux count变化 组件更新
function mapState2Props(state){
    return{
        count:state.counter.count,
        text:state.show_text.text
    }
}

//让actionAAA的调用直接能dispatch出去
function mapDispatchProps(dispatch){
    return bindActionCreators({actionAAA,actionText},dispatch)
}

const CounterConnect=connect(mapState2Props,mapDispatchProps)(Counter)

export default CounterConnect