
const { createStore, combineReducers } = require("redux")

const initState={count:0}

//reducer
const counter=(status=initState,action)=>{
    console.log("counter "+action.type)
    switch(action.type){
        case "AAA":
            return {count:100}
    }
    return status
}

const initState1={text:'123'}
const show_text=(status=initState1,action)=>{
    console.log("show_text "+action.type+"  "+action.text)
    switch(action.type){
        case "1":
            return {text:action.text}
    }
    return status
}

const reducer=combineReducers({
    counter,show_text
})

const store1=createStore(reducer)



export default store1 

